"use strict";
exports.__esModule = true;
var TableResult = /** @class */ (function () {
    function TableResult(form) {
        this.fields = new Array();
        this.form = form;
        this.table = document.createElement('table');
        this.thead = document.createElement('thead');
        this.tbody = document.createElement('tbody');
        this.tr = document.createElement('tr');
        this.objectTabs = new Array();
    }
    TableResult.prototype.create = function () {
        var titles = this.form.getth();
        this.table.className = "w3-table-all w3-centered";
        var thead = this.thead;
        var tbody = this.tbody;
        var thdel = document.createElement('th');
        var tr = this.tr;
        for (var i = 0; i < titles.length; i++) {
            var th = document.createElement('th');
            th.innerText = titles[i];
            tr.appendChild(th);
        }
        thdel.innerHTML = "USUŃ";
        tr.appendChild(thdel);
        thead.appendChild(tr);
        this.table.appendChild(thead);
        this.table.appendChild(tbody);
        this.form.valueElement.appendChild(this.table);
    };
    TableResult.prototype.updateExist = function () {
        var _this = this;
        var cat = JSON.parse(localStorage.getItem('list'));
        var object;
        var td;
        var _loop_1 = function (i) {
            var tr2 = document.createElement('tr');
            object = {
                imie: cat[i].imie,
                nazwisko: cat[i].nazwisko,
                email: cat[i].email,
                kierunek_studiow: cat[i].kierunek_studiow,
                elearning: cat[i].elearning,
                uwagi: cat[i].uwagi,
                data: cat[i].data,
                id: cat[i].id
            };
            var delbutton = document.createElement('button');
            for (var key in object) {
                td = document.createElement('td');
                td.innerHTML = object[key];
                if (key == "id") {
                    td.id = (object[key]);
                    td.innerHTML = "";
                    td.appendChild(delbutton);
                }
                tr2.appendChild(td);
            }
            this_1.objectTabs.push(object);
            this_1.tbody.appendChild(tr2);
            delbutton.addEventListener('click', function () { return _this.del(delbutton.parentElement.id); });
        };
        var this_1 = this;
        for (var i = 0; i < cat.length; i++) {
            _loop_1(i);
        }
        this.saveLC(this.objectTabs);
    };
    TableResult.prototype.update = function (id) {
        var _this = this;
        var tab2 = [];
        var tr2 = document.createElement('tr');
        var tddel = document.createElement('td');
        var td2;
        var delbutton = document.createElement('button');
        delbutton.id = id;
        delbutton.className = 'guziki';
        var user;
        this.form.fields.forEach(function (element) {
            tab2.push(element.getValue());
            td2 = document.createElement('td');
            td2.innerHTML = element.getValue();
            tr2.appendChild(td2);
        });
        for (var i = 0; i < tab2.length; i++) {
            user = {
                imie: tab2[0],
                nazwisko: tab2[1],
                email: tab2[2],
                kierunek_studiow: tab2[3],
                elearning: tab2[4],
                uwagi: tab2[5],
                data: tab2[6],
                id: id
            };
        }
        tab2.push(id);
        tddel.id = id;
        tddel.appendChild(delbutton);
        tr2.appendChild(tddel);
        this.tbody.appendChild(tr2);
        tddel.addEventListener('click', function () { return _this.del(tddel.id); });
        this.objectTabs.push(user);
        this.saveLC(this.objectTabs);
    };
    TableResult.prototype.del = function (id) {
        var elem = document.getElementById(id).parentElement;
        elem.parentNode.removeChild(elem);
        this.objectTabs = this.objectTabs.filter(function (person) { return person.id != id; });
        this.saveLC(this.objectTabs);
    };
    TableResult.prototype.saveLC = function (x) {
        localStorage.setItem("list", JSON.stringify(x));
    };
    return TableResult;
}());
exports.TableResult = TableResult;
